'use client';

import React, { useEffect, useMemo, useState } from 'react';
import { Search, Eye, Loader2 } from 'lucide-react';
import { API_URL } from '../api/apiURL';

/* --------------------------------------- */
/* USER AVATAR */
/* --------------------------------------- */
const UserAvatar = ({ user }) => {
  const initials =
    (user.firstName?.[0] || '').toUpperCase() +
    (user.lastName?.[0] || '').toUpperCase();

  return (
    <div className="flex items-center gap-3">
      {user.profileImage ? (
        <img
          src={user.profileImage}
          alt="profile"
          className="w-10 h-10 rounded-full object-cover"
        />
      ) : (
        <div className="w-10 h-10 bg-gray-300 rounded-full flex items-center justify-center text-sm font-semibold">
          {initials}
        </div>
      )}
      <div>
        <p className="font-semibold text-gray-900 text-sm">
          {user.firstName} {user.lastName}
        </p>
        <p className="text-xs text-gray-500">
          #{user.id || user._id?.slice(-6)} / {user.gender || 'Not Mentioned'}
        </p>
      </div>
    </div>
  );
};

/* --------------------------------------- */
/* STATUS HELPERS */
/* --------------------------------------- */
const getStatusDot = (status) =>
  ({
    approved: 'bg-green-500',
    pending: 'bg-yellow-500',
    reject: 'bg-red-500',
  }[status] || 'bg-gray-400');

const getStatusText = (status) =>
  ({
    approved: 'text-green-600',
    pending: 'text-yellow-600',
    reject: 'text-red-600',
  }[status] || 'text-gray-700');

/* --------------------------------------- */
/* DOCUMENT POPUP (SMALL CARD) */
/* --------------------------------------- */
const DocumentPopup = ({ user, onClose }) => {
  if (!user) return null;

  const aFront = user?.adhaarCard?.frontImage;
  const aBack = user?.adhaarCard?.backImage;
  const pan = user?.panCard?.frontImage;

  const handleBgClick = (e) => {
    if (e.target.id === 'doc-popup-bg') onClose();
  };

  return (
    <div
      id="doc-popup-bg"
      onClick={handleBgClick}
      className="fixed inset-0 bg-black/30 flex items-center justify-center z-50"
    >
      <div className="bg-white w-[280px] rounded-xl shadow-lg p-3 border">
        <h3 className="text-center font-semibold text-sm mb-3">Documents</h3>

        <div className="space-y-3 max-h-[320px] overflow-y-auto px-1">
          {aFront && (
            <div className="border rounded-xl shadow p-2">
              <img src={aFront} alt="Aadhar Front" className="w-full rounded-md" />
              <p className="text-xs text-center mt-1 font-medium">Aadhar Front</p>
            </div>
          )}

          {aBack && (
            <div className="border rounded-xl shadow p-2">
              <img src={aBack} alt="Aadhar Back" className="w-full rounded-md" />
              <p className="text-xs text-center mt-1 font-medium">Aadhar Back</p>
            </div>
          )}

          {pan && (
            <div className="border rounded-xl shadow p-2">
              <img src={pan} alt="PAN Card" className="w-full rounded-md" />
              <p className="text-xs text-center mt-1 font-medium">PAN Card</p>
            </div>
          )}

          {!aFront && !aBack && !pan && (
            <p className="text-center text-gray-500 text-xs">No Documents Uploaded</p>
          )}
        </div>

        <button
          onClick={onClose}
          className="w-full mt-3 bg-gray-900 text-white py-1.5 rounded-md text-sm"
        >
          Close
        </button>
      </div>
    </div>
  );
};

/* --------------------------------------- */
/* MAIN COMPONENT */
/* --------------------------------------- */
export default function UserModerationDashboard() {
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState(true);

  // UI state
  const [search, setSearch] = useState('');
  const [status, setStatus] = useState('Status');
  const [gender, setGender] = useState('Gender');
  const [popupUser, setPopupUser] = useState(null);

  // pagination
  const [currentPage, setCurrentPage] = useState(1);
  const perPage = 10;

  // sorting
  const [sort, setSort] = useState({ field: '', dir: 'asc' });

  // counts
  const [counts, setCounts] = useState({
    total: 0,
    pending: 0,
    approved: 0,
    rejected: 0,
  });

  // fetch users
  useEffect(() => {
    const load = async () => {
      try {
        const res = await fetch(`${API_URL}/admin/user-verify`);
        const json = await res.json();
        const data = Array.isArray(json?.data) ? json.data : [];
        setUsers(data);

        setCounts({
          total: data.length,
          pending: data.filter((u) => u.adminApprovel === 'pending').length,
          approved: data.filter((u) => u.adminApprovel === 'approved').length,
          rejected: data.filter((u) => u.adminApprovel === 'reject').length,
        });
      } catch (e) {
        console.error('API error', e);
      } finally {
        setLoading(false);
      }
    };
    load();
  }, []);

  // filter + sort
  const filtered = useMemo(() => {
    let data = [...users];

    if (search) {
      const q = search.toLowerCase();
      data = data.filter(
        (u) =>
          u.id?.toLowerCase().includes(q) ||
          u._id?.toLowerCase().includes(q) ||
          u.firstName?.toLowerCase().includes(q) ||
          u.lastName?.toLowerCase().includes(q)
      );
    }

    if (status !== 'Status') data = data.filter((u) => u.adminApprovel === status);
    if (gender !== 'Gender') data = data.filter((u) => u.gender === gender);

    if (sort.field === 'name') {
      data.sort((a, b) => {
        const A = `${a.firstName ?? ''} ${a.lastName ?? ''}`.toLowerCase();
        const B = `${b.firstName ?? ''} ${b.lastName ?? ''}`.toLowerCase();
        return sort.dir === 'asc' ? A.localeCompare(B) : B.localeCompare(A);
      });
    } else if (sort.field === 'date') {
      data.sort((a, b) => {
        const da = new Date(a.createdAt || 0).getTime();
        const db = new Date(b.createdAt || 0).getTime();
        return sort.dir === 'asc' ? da - db : db - da;
      });
    }

    return data;
  }, [users, search, status, gender, sort]);

  const totalPages = Math.max(1, Math.ceil(filtered.length / perPage));
  // clamp currentPage
  useEffect(() => {
    if (currentPage > totalPages) setCurrentPage(totalPages);
  }, [totalPages, currentPage]);

  const pageData = filtered.slice((currentPage - 1) * perPage, currentPage * perPage);

  const getDocumentsText = (u) => {
    const docs = [];
    if (u.adhaarCard?.frontImage) docs.push('Aadhar Card');
    if (u.adhaarCard?.backImage) docs.push('Aadhar Back');
    if (u.panCard?.frontImage) docs.push('PAN Card');
    return docs.length ? docs.join(', ') : 'No Document Submitted';
  };

  // header arrow click helpers
  const toggleSort = (field) => {
    setSort((prev) => {
      if (prev.field !== field) return { field, dir: 'asc' };
      return { field, dir: prev.dir === 'asc' ? 'desc' : 'asc' };
    });
  };

  return (
    <div className="px-6 py-6">

      {/* top cards */}
      <div className="flex justify-center gap-12 mb-8 flex-wrap">
        {[
          ['Total Request This Week', counts.total],
          ['Pending Verification', counts.pending],
          ['Approved This Week', counts.approved],
          ['Rejected Due To Mismatch', counts.rejected],
        ].map(([label, value]) => (
          <div key={label} className="w-[220px] text-center border shadow rounded-2xl p-6 bg-white">
            <p className="font-semibold text-sm">{label}</p>
            <h2 className="text-3xl font-black mt-3">{value}</h2>
          </div>
        ))}
      </div>

      {/* table card */}
      <div className="max-w-7xl mx-auto p-5 border rounded-2xl bg-white shadow">
        {/* search & filters */}
        <div className="border rounded-xl p-3 flex justify-between items-center">
          <div className="relative w-[320px]">
            <Search className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-400 w-5" />
            <input
              className="w-full pl-10 pr-3 py-2 border rounded-lg text-sm"
              placeholder="Search By User ID"
              value={search}
              onChange={(e) => {
                setSearch(e.target.value);
                setCurrentPage(1);
              }}
            />
          </div>

          <div className="flex gap-3">
            <select
              className="border px-3 py-2 bg-gray-100 rounded-md"
              value={status}
              onChange={(e) => {
                setStatus(e.target.value);
                setCurrentPage(1);
              }}
            >
              <option>Status</option>
              <option value="approved">approved</option>
              <option value="pending">pending</option>
              <option value="reject">reject</option>
            </select>

            <select
              className="border px-3 py-2 bg-gray-100 rounded-md"
              value={gender}
              onChange={(e) => {
                setGender(e.target.value);
                setCurrentPage(1);
              }}
            >
              <option>Gender</option>
              <option>Male</option>
              <option>Female</option>
            </select>
          </div>
        </div>

        {/* table */}
        <div className="overflow-x-auto mt-5 border rounded-xl">
          {loading ? (
            <div className="flex justify-center py-10">
              <Loader2 className="animate-spin w-6 h-6" />
            </div>
          ) : (
            <table className="w-full text-sm">
              <thead className="bg-gray-100">
                <tr>
                  <th
                    className="px-4 py-3 text-left cursor-pointer select-none"
                    onClick={() => toggleSort('name')}
                  >
                    <div className="flex items-center gap-2">
                      <span>Reported User</span>
                      <span className={`sort-arrows ${sort.field === 'name' ? 'active' : ''}`}>
                        <svg width="12" height="12" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                          <path d="M6 14l6-6 6 6" stroke="currentColor" strokeWidth="1.8" strokeLinecap="round" strokeLinejoin="round"/>
                          <path d="M6 10l6 6 6-6" stroke="currentColor" strokeWidth="1.8" strokeLinecap="round" strokeLinejoin="round" opacity="0.6"/>
                        </svg>
                      </span>
                    </div>
                  </th>

                  <th
                    className="px-4 py-3 text-left cursor-pointer select-none"
                    onClick={() => toggleSort('date')}
                  >
                    <div className="flex items-center gap-2">
                      <span>Report Date</span>
                      <span className={`sort-arrows ${sort.field === 'date' ? 'active' : ''}`}>
                        <svg width="12" height="12" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                          <path d="M6 14l6-6 6 6" stroke="currentColor" strokeWidth="1.8" strokeLinecap="round" strokeLinejoin="round"/>
                          <path d="M6 10l6 6 6-6" stroke="currentColor" strokeWidth="1.8" strokeLinecap="round" strokeLinejoin="round" opacity="0.6"/>
                        </svg>
                      </span>
                    </div>
                  </th>

                  <th className="px-4 py-3 text-left">Documents Submitted</th>
                  <th className="px-4 py-3 text-left">Status</th>
                  <th className="px-4 py-3 text-left">Actions</th>
                </tr>
              </thead>

              <tbody className="divide-y">
                {pageData.map((u) => (
                  <tr key={u._id} className="hover:bg-gray-50">
                    <td className="px-4 py-3">
                      <UserAvatar user={u} />
                    </td>

                    <td className="px-4 py-3">
                      {new Date(u.createdAt).toLocaleDateString('en-GB', {
                        day: '2-digit',
                        month: 'long',
                        year: 'numeric',
                      })}
                    </td>

                    <td className="px-4 py-3">{getDocumentsText(u)}</td>

                    <td className="px-4 py-3">
                      <div className="flex items-center gap-2">
                        <div className={`w-3 h-3 rounded-full ${getStatusDot(u.adminApprovel)}`} />
                        <span className={getStatusText(u.adminApprovel)}>{u.adminApprovel}</span>
                      </div>
                    </td>

                    <td className="px-4 py-3">
                      <button
                        onClick={() => setPopupUser(u)}
                        className="flex items-center gap-1 text-blue-700"
                        aria-label={`View documents for ${u.firstName} ${u.lastName}`}
                      >
                        <Eye size={14} /> View
                      </button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          )}
        </div>

        {/* pagination (screenshot style) */}
        <div className="flex justify-center mt-5 items-center gap-3">
          <button
            onClick={() => setCurrentPage((p) => Math.max(1, p - 1))}
            disabled={currentPage === 1}
            className="text-gray-700 disabled:opacity-40"
          >
            ‹
          </button>

          <div className="flex items-center gap-2 text-sm">
            {Array.from({ length: Math.min(totalPages, 4) }, (_, i) => i + 1).map((p) => (
              <button
                key={p}
                onClick={() => setCurrentPage(p)}
                className={`px-2 ${currentPage === p ? 'font-bold text-black underline' : 'text-gray-600'}`}
              >
                {p}
              </button>
            ))}

            {totalPages > 4 && <span className="text-gray-500">.....</span>}

            {totalPages > 4 && (
              <button
                onClick={() => setCurrentPage(totalPages)}
                className={`px-2 ${currentPage === totalPages ? 'font-bold text-black underline' : 'text-gray-600'}`}
              >
                {totalPages}
              </button>
            )}
          </div>

          <button
            onClick={() => setCurrentPage((p) => Math.min(totalPages, p + 1))}
            disabled={currentPage === totalPages}
            className="text-gray-700 disabled:opacity-40"
          >
            ›
          </button>
        </div>
      </div>

      {/* popup */}
      {popupUser && <DocumentPopup user={popupUser} onClose={() => setPopupUser(null)} />}

      {/* small local styles for arrows and animation */}
      <style jsx global>{`
        .sort-arrows { display:inline-flex; align-items:center; color: #9CA3AF; }
        .sort-arrows svg { transition: transform .18s ease, color .18s ease; }
        .sort-arrows.active svg { color: #111827; transform: rotate(0deg); }
        /* active state visual: rotate depending on dir */
        /* we also change opacity of the lower path for a clearer active look */
        /* but for simplicity we use same shape & tint. */

        @keyframes fadeIn {
          from { opacity: 0; transform: scale(0.98); }
          to { opacity: 1; transform: scale(1); }
        }
        .animate-fadeIn { animation: fadeIn 0.18s ease-out; }
      `}</style>
    </div>
  );
}

/* --------------------------------------- */
/* small helper used in component body (defined after to keep top clean) */
/* --------------------------------------- */
function getDocumentsText(user) {
  const docs = [];
  if (user.adhaarCard?.frontImage) docs.push('Aadhar Card');
  if (user.adhaarCard?.backImage) docs.push('Aadhar Back');
  if (user.panCard?.frontImage) docs.push('PAN Card');
  return docs.length ? docs.join(', ') : 'No Document Submitted';
}
